import React, { useState } from "react";
import styled from "styled-components";
import Axios from "axios";
import Login from './modules/Login'
import Head from './modules/head'
import Singup from './modules/Signup'
import { firebase, auth } from './firebase';
import Logout from "./modules/logout";

const Container = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 380px;
  padding: 20px 10px;
  margin: auto;
  border-radius: 4px;
  box-shadow: 0 3px 6px 0 #555;
  background: white;
  font-family: Montserrat;
`;

const AppLabel = styled.span`
  color: black;
  margin: 20px auto;
  font-size: 18px;
  font-weight: bold;
`;



function App() {
  const [Ac, setAc] = useState(true)
  const [Idr, setIdr] = useState('')
  const [Psr, setPsr] = useState('')
  const [Id, setId] = useState('')
  const [Ps, setPs] = useState('')
  const [final, setfinal] = useState('')
  const [show, setshow] = useState(false)
  const [otp, setOtp] = useState('')
  const [login, setlogin] = useState(false)
  const reg = () => {
    let verify = new firebase.auth.RecaptchaVerifier('recaptcha-container');
    auth.signInWithPhoneNumber(Idr, verify).then((result) => {
      setfinal(result);
      setshow(true);
    })
      .catch((err) => {
        alert(err);
        window.location.reload()
      });
  }

  const ValidateOtp = () => {
    if (otp === null || final === null)
      return;
    final.confirm(otp).then((result) => {
      // success
      Axios.post('https://login0.herokuapp.com/rg', {
        id: Idr,
        pass: Psr
      }).then((res) => {
        res.data[0].singup ? alert("Sign Up successful. Log in to continue") : alert("Number already registered. try forgot password")
        setshow(false);
        setAc(true);
      })
    }).catch((err) => {
      alert("Wrong code");
    })
  }
  const li = () => {
    Axios.post('https://login0.herokuapp.com/li', {
      id: Id,
      pass: Ps
    }).then((res) => {
      res.data[0].login ? setlogin(true) : alert("Invalid Id or Password")
      setshow(false);
    })
  }

  return (
    <Container>
      <AppLabel>React Login App</AppLabel>
      {login ? <Logout id={Id} setlogin={setlogin} /> : <>
        <Head Ac={Ac} setAc={setAc} />{
          Ac ?
            <Login setId={setId} setPs={setPs} li={li} /> :
            <Singup setIdr={setIdr} setOtp={setOtp} setPsr={setPsr} reg={reg} ValidateOtp={ValidateOtp} show={show} />
        }</>}
    </Container>
  );
}

export default App;
