import React from 'react'
import styled from "styled-components";

const SearchBox = styled.form`
    width: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
    & button {
        display: block;
        margin: 4px auto;
        font-size: 14px;
        background-color: black;
        color: white;
        font-family: Montserrat;
        font-weight: bold;
        padding: 2px 10px;
        border-radius: 6px;
        border: none;
    }
  `;
const AppLabel = styled.span`
  color: black;
  margin: 20px auto;
  font-size: 20px;
  font-weight: bold;
`;
export default function Logout(props) {
    const { id, setlogin } = props;
    return (
        <SearchBox >
            <AppLabel>Welcome {id}</AppLabel>
            <button onClick={(e) => { e.preventDefault(); setlogin(false) }}>Log Out</button>
        </SearchBox>
    )
}
