import React from 'react'
import styled from "styled-components";

const Header = styled.div`
    width: 80%;
    margin: 4px 10%;
    & button {
        width: 50%;
        border: none;    
        padding-bottom: 14px;
        background-color: white;
        font-family: Montserrat;
        font-weight: bold;
        
    }
    & .ac {
        border-bottom: 2px solid;
    }
`;
export default function Head(props) {
    const { Ac, setAc } = props;
    return (
        <Header>
            <button onClick={() => { setAc(true) }} className={Ac ? 'ac' : ''}>Log In</button>
            <button onClick={() => { setAc(false) }} className={Ac ? '' : 'ac'}>Sign Up</button>
        </Header>
    )
}
