import React from 'react'
import styled from "styled-components";

const SearchBox = styled.form`
    width: 80%;
    & input {
        width: 90%;
        margin: 4px 5%;
        border: 2px solid;
        border-radius: 4px;
        font-family: Montserrat;
        font-weight: bold;
        font-size: 14px;
    }& p {
        width: 90%;
        margin: 4px 5%;
        font-family: Montserrat;
        font-weight: bold;
        font-size: 8px;
    }
    & button {
        display: block;
        margin: 4px auto;
        font-size: 14px;
        background-color: black;
        color: white;
        font-family: Montserrat;
        font-weight: bold;
        padding: 2px 10px;
        border-radius: 6px;
        border: none;
    }
  `;
export default function Login(props) {
    const { setIdr, setPsr, reg, ValidateOtp, show, setOtp } = props;
    return (
        <SearchBox >

            {show ? <>
                <input type="Number" placeholder="OTP"
                    onChange={(e) => setOtp(e.target.value)} />
                <button onClick={(e) => { e.preventDefault(); ValidateOtp() }}>Verify</button>
            </> : <><input
                onChange={(e) => setIdr(e.target.value)}
                placeholder="Phone No." type="text"
            />
                <p>(Note: Enter with country code eg +918888888888)</p>
                <input type="password" placeholder="Password"
                    onChange={(e) => setPsr(e.target.value)}
                />
                <input type="password" placeholder="Re-enter Password" />
                <button onClick={(e) => { e.preventDefault(); reg() }}>Sign Up</button>
                <div id="recaptcha-container"></div></>}
        </SearchBox>
    )
}
